<template>
  <div class="recommend">
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <div
        v-for="(item,index) in list"
        :is="mod(item.blocks)"
        :content="item.blocks"
        :key="index"
        :noblockline="item.noblockline"
      ></div>
    </van-list>
  </div>
</template>

<script>
import { List } from "vant";
import axios from "axios";
import compon from "../component/rotation2"; //轮播
import videoCard from "../component/videoCard"; //视频轮播
import horizontal from "../component/horizontal"; //水平横拨
import playList from "../component/playList"; //应用列表
import entrance from "../component/entrance"; //入口组件
import Ads from "../component/Ads"; //广告
export default {
  props: {
    requestid: {
      type: String,
      default: "mmap_index_v1"
    }
  },
  components: {
    compon,
    horizontal,
    videoCard,
    playList,
    entrance,
    Ads,
    [List.name]: List
  },
  computed: {
    mod() {
      return function(blocks) {
        if (blocks[0].type == 24) {
          return "compon";
        } else if (blocks[0].type == 25) {
          // return "entrance";
          return "";
        } else if (blocks[0].type == 1) {
          if (blocks[1].type == 50) {
            return "horizontal";
          } else if (blocks[1] == 24) {
            return "compon";
          } else if (blocks[1].type == 57) {
            return "videoCard";
          } else if (blocks[1].type == 6) {
            return "playList";
          }
        } else if (blocks[0].type == 6) {
          return "playList";
        } else if (blocks[0].type == 57) {
          return "videoCard";
        } else if (blocks[0].type == 50) {
          return "horizontal";
        }
        console.log(blocks);
      };
    }
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      active: 0
    };
  },
  mounted() {
    console.log("prop", this.requestid);
  },
  methods: {
    onLoad() {
      // 异步更新数据
      this.loading = true;
      axios({
        method: "post",
        url: process.env.VUE_APP_API + "getCard.do",
        data: {
          requestid: this.requestid
        },
        headers: {
          "Content-Type": "application/json",
          appname: "MM7.3.2.001.01_CTAndroid_JT",
          ua: "android-24-1080x2040-RNE-AL00"
        }
      })
        .then(response => {
          var packageAwardList = response.data.cards;
          this.list = response.data.cards;
          // }
          // 加载状态结束
          this.loading = false;
          // 数据全部加载完成
          this.finished = true;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>

<style lang="less">
.grid {
  .iconImg {
    width: 54px;
    height: 54px;
    margin: 0 10px;
  }
  .iconName {
    margin-top: 10px;
    font-size: 12.5px;
  }
}
.separate {
  height: 6px;
  background: rgba(242, 242, 242, 1);
}
.van-cell--large .van-cell__title {
  font-size: 18px;
  margin-bottom: 5px;
}
</style>
